.login {
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login_logo {
    margin-top: 30px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
  }

.login_container {
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}

.login_container > h1 {
    font-weight: 400;
    margin-bottom: 20px;
  }

.login_container > form > h6 {
    margin-bottom: 5px;
}

.login_container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
  }

.login_container > p {
    margin-top: 15px;
    font-size: 12px;
}

.login_signInButton {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
  }

  .login_registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
  }
  .footerPanel4_login{
    background-color: white;
    color: #0066c0;
    height: 80px;
    font-size: 0.8rem;
    text-align: center;

}
.pages_login{
  padding-top: 10px;
}
.copyright_login{
  padding-top: 3px;

}

.footerPanel4_login a{
  text-decoration: none;
  color: #0066c0;
  padding: 8px;
 
}
.footerPanel4_login a:hover{
  text-decoration: underline;
  color: orange;
 
}